import React, { useEffect } from "react";
import PropTypes from "prop-types";
import { Tooltip } from "bootstrap";

const Button = ({ label, type, size, onClick, disabled }) => {
  // Xác định các class cho button dựa trên props
  const buttonClass = `btn btn-${type} btn-${size}`;

  return (
    <button type="button" className={buttonClass} onClick={onClick} disabled={disabled}>
      {label}
    </button>
  );
};

// PropTypes để đảm bảo các props truyền vào đúng kiểu
Button.propTypes = {
  label: PropTypes.string.isRequired, // Nội dung hiển thị trên button
  type: PropTypes.string, // Loại button (primary, secondary, v.v.)
  size: PropTypes.string, // Kích thước button (sm, lg, hoặc không có)
  onClick: PropTypes.func, // Hàm xử lý khi click button
  disabled: PropTypes.bool, // Có bị disabled hay không
};

// Giá trị mặc định cho props
Button.defaultProps = {
  type: "primary", // Loại mặc định là "primary"
  size: "", // Kích thước mặc định (không có)
  onClick: () => {}, // Hàm xử lý click mặc định (không làm gì)
  disabled: false, // Mặc định không bị disabled
};

const ButtonWithIcon = ({ label, type, size, icon, onClick, disabled, title }) => {
  // Xác định các class cho button dựa trên props
  const buttonClass = `btn btn-${type} btn-${size}`;

  //   useEffect(() => {
  //     // Khởi tạo tooltip sau khi component được render
  //     const tooltipTriggerList = document.querySelectorAll('[data-bs-toggle="tooltip"]');
  //     tooltipTriggerList.forEach((tooltipTriggerEl) => {
  //       new Tooltip(tooltipTriggerEl); // Khởi tạo tooltip cho từng phần tử
  //     });
  //   }, []); // [] chỉ chạy một lần khi component được mount

  return (
    <button
      type="button"
      className={buttonClass}
      onClick={onClick}
      disabled={disabled}
      data-bs-toggle="tooltip"
      data-bs-placement="top"
      title={title}
    >
      {icon && <i className={`bi-${icon} me-2`}></i>} {/* Hiển thị icon nếu có */}
      {label}
    </button>
  );
};

// PropTypes để đảm bảo các props truyền vào đúng kiểu
ButtonWithIcon.propTypes = {
  label: PropTypes.string.isRequired, // Nội dung hiển thị trên button
  type: PropTypes.string, // Loại button (primary, secondary, v.v.)
  size: PropTypes.string, // Kích thước button (sm, lg, hoặc không có)
  icon: PropTypes.string, // Tên của icon (Bootstrap Icons)
  onClick: PropTypes.func, // Hàm xử lý khi click button
  disabled: PropTypes.bool, // Có bị disabled hay không
  title: PropTypes.string, // Dữ liệu liên quan đến button
};

// Giá trị mặc định cho props
ButtonWithIcon.defaultProps = {
  type: "primary", // Mặc định là button "primary"
  size: "", // Không có kích thước mặc định (có thể là 'sm' hoặc 'lg')
  icon: "", // Không có icon mặc định
  onClick: () => {}, // Hàm xử lý mặc định là không làm gì
  disabled: false, // Mặc định không disabled
};

const ButtonWithBadge = ({ label, type, size, active, onClick, disabled, badge }) => {
  // Xác định các class cho button dựa trên props
  const buttonClass = `btn btn-${type} btn-${size} ${active}`;

  return (
    <button type="button" className={buttonClass} onClick={onClick} disabled={disabled}>
      {label} {badge && <span className="badge text-bg-secondary">{badge}</span>}
    </button>
  );
};

// PropTypes để đảm bảo các props truyền vào đúng kiểu
ButtonWithBadge.propTypes = {
  label: PropTypes.string.isRequired, // Nội dung hiển thị trên button
  type: PropTypes.string, // Loại button (primary, secondary, v.v.)
  size: PropTypes.string, // Kích thước button (sm, lg, hoặc không có)
  active: PropTypes.string,
  onClick: PropTypes.func, // Hàm xử lý khi click button
  disabled: PropTypes.bool, // Có bị disabled hay không
  badge: PropTypes.oneOfType([PropTypes.string, PropTypes.number]), // Nội dung hiển thị trên badge
};

// Giá trị mặc định cho props
ButtonWithBadge.defaultProps = {
  type: "primary", // Loại mặc định là "primary"
  size: "", // Kích thước mặc định (không có)
  onClick: () => {}, // Hàm xử lý click mặc định (không làm gì)
  disabled: false, // Mặc định không bị disabled
  badge: null, // Mặc định không có badge
};

export { Button, ButtonWithIcon, ButtonWithBadge };
