/* global console, document, Excel, Office */

export async function getDataTable(sheetName, tableName) {
  return await Excel.run(async (context) => {
    // Get the specified sheet
    const sheet = context.workbook.worksheets.getItem(sheetName);
    // Get the specified table
    const table = sheet.tables.getItem(tableName);
    // Get the range of the table
    const range = table.getRange();

    // Load the values and hyperlinks of the entire table range
    range.load(["values"]);

    await context.sync();

    const allData = [];

    for (let rowIndex = 0; rowIndex < range.values.length; rowIndex++) {
      const rowData = [];
      for (let colIndex = 0; colIndex < range.values[rowIndex].length; colIndex++) {
        let cellRange = range.getCell(rowIndex, colIndex);
        cellRange.load("hyperlink"); // Load hyperlink cho ô cụ thể
        rowData.push(cellRange);
      }
      allData.push(rowData);
    }

    return context.sync().then(function () {
      const result = allData.map((row, rowIndex) => {
        return row.map((cellRange, colIndex) => {
          let hyperlink = cellRange.hyperlink;
          return hyperlink ? hyperlink.address : range.values[rowIndex][colIndex];
        });
      });

      console.log(result);
      return result;
    });
  });
}

export function insertDataTable(sheetName, tableName, updateDict) {
  return new Promise((resolve, reject) => {
    Excel.run((context) => {
      // Lấy sheet được chỉ định
      let sheet = context.workbook.worksheets.getItem(sheetName);
      // Lấy table được chỉ định
      let table = sheet.tables.getItem(tableName);
      // Lấy range của table
      let range = table.getRange();
      range.load("values");

      return context.sync().then(() => {
        // Lấy header row
        let headerRow = range.values[0];
        // Tạo một mảng mới cho hàng cần thêm
        let newRow = new Array(headerRow.length).fill("");

        // Cập nhật các giá trị từ updateDict
        for (let columnName in updateDict) {
          if (Object.prototype.hasOwnProperty.call(updateDict, columnName)) {
            let columnIndex = headerRow.indexOf(columnName);
            if (columnIndex !== -1) {
              newRow[columnIndex] = updateDict[columnName];
            } else {
              reject(`Column name "${columnName}" not found`);
              return;
            }
          }
        }

        // Thêm hàng mới vào cuối table
        table.rows.add(null, [newRow]);

        return context.sync().then(() => {
          resolve("Data inserted successfully");
        });
      });
    }).catch((error) => {
      reject(error);
    });
  });
}

// Function to convert a 2D array into JSON format
export function convertToJSON(array) {
  // Extract the first row of the array as headers (keys for the JSON objects)
  const headers = array[0];
  // Extract the rest of the rows as data
  const data = array.slice(1);

  // Map through each row in the data array
  return data.map((row, index) => {
    let obj = {};
    // Iterate over each value in the row and assign it to the corresponding header
    row.forEach((value, i) => {
      obj[headers[i]] = value; // Assign the value to the corresponding key (header)
    });
    //obj["%A.Completed"] = obj["%A.Completed"] * 100;
    obj.rowIndex = index + 1;
    return obj; // Return the object for each row
  });
}

/**
 * Converts an Excel date number to a JavaScript Date object.
 * @param {number} excelDate - The Excel date number.
 * @returns {string} The date in 'YYYY-MM-DD' format, or an empty string if input is invalid.
 */
export function excelDateToJSDate(excelDate) {
  // Check if the input is valid
  if (!excelDate || isNaN(excelDate)) {
    return "";
  }
  // Convert Excel date to JavaScript Date
  const utcDate = new Date((excelDate - 25569) * 86400 * 1000);
  // Return the date in 'YYYY-MM-DD' format
  return utcDate.toISOString().split("T")[0];
}

