function updateTimeSheet(context, table, data) {
  const row = table.rows.getItemAt(Number(data.rowIndex));
  let totalTime = calculateWorkTime(data.startTime, data.endTime);
  row.values = [[null, null, null, data.startTime, data.endTime, totalTime, data.desc]];
}

function insertTimeSheet(context, table, data) {
  let totalTime = calculateWorkTime(data.StartTime, data.EndTime);
  table.rows.add(null, [[data.Date, data.TaskID, data.Account, data.StartTime, data.EndTime, totalTime, data.Desc]]);
}
export function calculateWorkTime(startTime, endTime) {
  // Kiểm tra nếu startTime và endTime đều không rỗng
  if (startTime && endTime) {
    // Chuyển đổi thời gian từ chuỗi sang đối tượng Date
    const start = new Date(`1970-01-01T${startTime}:00`);
    const end = new Date(`1970-01-01T${endTime}:00`);
    const lunchStart = new Date(`1970-01-01T12:00:00`);
    const lunchEnd = new Date(`1970-01-01T13:00:00`);

    // Tính toán thời gian làm việc
    let totalTime = (end - start) / (1000 * 60 * 60); // Chuyển sang giờ

    // Trừ thời gian nghỉ trưa nếu nó nằm trong khoảng thời gian làm việc
    if (start < lunchStart && end > lunchEnd) {
      totalTime -= 1; // Trừ 1 giờ nghỉ trưa
    } else if (start < lunchEnd && end > lunchEnd) {
      totalTime -= (lunchEnd - start) / (1000 * 60 * 60); // Trừ thời gian từ start đến hết nghỉ trưa
    } else if (start < lunchStart && end > lunchStart) {
      totalTime -= (end - lunchStart) / (1000 * 60 * 60); // Trừ thời gian từ bắt đầu nghỉ trưa đến end
    }

    return totalTime;
  } else {
    return 0; // Nếu startTime hoặc endTime rỗng, trả về 0
  }
}

// Hàm cập nhật dữ liệu vào đúng vị trí trong bảng Excel dựa trên headerName và rowIndex
export const updateExcelData = async (sheetName, tableName, jsonData) => {
  await Excel.run(async (context) => {
    const sheet = context.workbook.worksheets.getItem(sheetName);

    // Lấy bảng theo tên
    const table = sheet.tables.getItem(tableName);

    // Lấy toàn bộ hàng và cột của bảng
    const headersRange = table.getHeaderRowRange();
    const dataRange = table.getDataBodyRange();

    // Đồng bộ dữ liệu để đảm bảo có thể truy cập được
    headersRange.load("values");
    dataRange.load("values");
    await context.sync();

    // Lấy header từ dòng đầu tiên (cột tiêu đề)
    const headers = headersRange.values[0];

    // Lấy `rowIndex` từ jsonData
    const rowIndex = jsonData.rowIndex;

    // Nếu rowIndex < 1 hoặc vượt quá số hàng của bảng thì trả về lỗi
    if (rowIndex < 1 || rowIndex > dataRange.values.length) {
      console.error("Invalid rowIndex");
      return;
    }

    // Duyệt qua từng thuộc tính trong jsonData (ngoại trừ rowIndex)
    Object.keys(jsonData).forEach((key) => {
      if (key !== "rowIndex") {
        // Tìm chỉ số cột (index của header) tương ứng với tên key
        const columnIndex = headers.indexOf(key);
        if (columnIndex !== -1) {
          // Cập nhật giá trị vào đúng ô theo rowIndex và columnIndex
          dataRange.getCell(rowIndex - 1, columnIndex).values = [[jsonData[key]]];
        }
      }
    });

    // Đồng bộ lại để áp dụng thay đổi
    await context.sync();
  }).catch((error) => {
    console.log("Error updating data: " + error);
  });
};
export const updateExcelDataBatch = async (sheetName, tableName, jsonDataList) => {
  await Excel.run(async (context) => {
    const sheet = context.workbook.worksheets.getItem(sheetName);

    // Lấy bảng theo tên
    const table = sheet.tables.getItem(tableName);

    // Lấy toàn bộ hàng và cột của bảng
    const headersRange = table.getHeaderRowRange();
    const dataRange = table.getDataBodyRange();

    // Đồng bộ dữ liệu để đảm bảo có thể truy cập được
    headersRange.load("values");
    dataRange.load("values");
    await context.sync();

    // Lấy header từ dòng đầu tiên (cột tiêu đề)
    const headers = headersRange.values[0];

    // Duyệt qua từng object trong jsonDataList
    jsonDataList.forEach((jsonData) => {
      // Lấy `rowIndex` từ jsonData
      const rowIndex = jsonData.rowIndex;

      // Nếu rowIndex < 1 hoặc rowIndex > dataRange.values.length thì trả về lỗi
      if (rowIndex < 1 || rowIndex > dataRange.values.length) {
        console.error("Invalid rowIndex for data:", jsonData);
        return;
      }

      // Duyệt qua từng thuộc tính trong jsonData (ngoại trừ rowIndex)
      Object.keys(jsonData).forEach((key) => {
        if (key !== "rowIndex") {
          // Tìm chỉ số cột (index của header) tương ứng với tên key
          const columnIndex = headers.indexOf(key);
          if (columnIndex !== -1) {
            // Cập nhật giá trị vào đúng ô theo rowIndex và columnIndex
            dataRange.getCell(rowIndex - 1, columnIndex).values = [[jsonData[key]]];
          }
        }
      });
    });

    // Đồng bộ lại để áp dụng thay đổi
    await context.sync();
  }).catch((error) => {
    console.log("Error updating data: " + error);
  });
};
